.App-header{
  display: flex;
  background: #090909;
  color: #fff;
  font-size: 0.6rem;
  padding-left: 20px;
}

div{
  box-sizing: border-box;
}
.list{
  padding: 20px;
  border-bottom: 1px dashed #ccc;
}
h3 span{
  font-size: 0.5rem;
  font-weight: normal;
}
h3 span:first-child{
  cursor: pointer; color: #06f;
}
.write-box{
  display: flex;
  justify-content: center;
}
.write-box input{
  padding: 4px 8px;
  border: 1px solid #ccc;
  height: 20px;
}
.write-box button{
  height: 30px; border: 0;
  background: #090909;
  color: #fff;
  border-radius: 4px;
  margin-left: 8px;
}
